.footer {
	height: 90px;
	background: #fff;
	padding: 0 40px;
	display: flex;
	justify-content: space-between;
  align-items: center;
  max-width: 1440px;
  margin: auto;
}
.footer .logo {
	width: 250px;
	height: 90px;
	background: url(../../images/logo.svg) no-repeat center;
	background-size: contain;
}
.footer .link {
	font-family: 'GothamPro';
	font-size: 16px;
	line-height: 15px;
	color: #2B3137;
	text-decoration: none;
	padding: 0 15px;
}
.footer .link.active {
	font-family: 'GothamPro-Bold';
	color: #E85656;
}

@media (max-width: 992px) {
	.footer {
    height: 100px;
    padding: 20px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
	}
	.footer .logo {
	  height: 30px;
	  width: 170px;
	  display: block;
	}
	.footer .link {
    font-size: 14px;
    line-height: 30px;
    padding: 0 5px;
	}
	.footer .nav {
		width: 100%;
		text-align: center;
	}




}