.hero {
	background: url(../images/backgrounds/hero.png) no-repeat center left;
	background-color: #2B3137;
  max-width: 1440px;
  margin: auto;
	display: flex;
  justify-content: space-between;
}
.headSide {
	width: calc(100% - 450px);
}
.headSide h1 {
	font-family: 'GothamPro-Bold';
	font-size: 120px;
	line-height: 100%;
	color: #fff;
	padding: 130px 40px;
}
.headSide h1 span {
	text-shadow: 7px 7px 0px #e85656;
}
.textSide {
	width: 450px;
	min-height: 100%;
	background: #E85656;
	padding: 40px 30px;
	color: #fff;
}
.textSide p {
	font-family: 'GothamPro';
	font-size: 22px;
	line-height: 35px;
}
.textSide .button {
	width: 190px;
	height: 45px;
	line-height: 45px;
	border: 1px solid #fff;
	box-shadow: none;
	background: transparent;
	color: #fff;
	text-align: center;
	font-family: 'GothamPro';
	font-size: 16px;
	display: block;
	margin-top: 30px;
	text-decoration: none;
	border-radius: 12px;
}
.textSide .button:hover {
	border: 1px solid #fff;
	background: #fff;
	color: #E85656;
}
.filters {
	height: 120px;
  max-width: 1440px;
  margin: auto;
	display: flex;
  align-items: center;
  margin: auto;
  padding: 0 40px;
}
.filters h1 {
	font-family: 'GothamPro-Bold';
	font-size: 18px;
	color: #000;
	margin-right: 10px;
}
.filters .filter {
	width: 220px;
	height: 40px;
	line-height: 40px;
	border: 1px solid #C8CDD2;
	border-radius: 12px;
	margin: 0px 10px;
	font-family: 'GothamPro';
	font-size: 18px;
	text-align: left;
	padding-left: 10px;
	position: relative;
	background: url(../images/icons/arrow.svg) no-repeat right 15px center;
	cursor: pointer;
}
.search input {
	width: 280px;
	height: 40px;
	line-height: 40px;
	border: 1px solid #C8CDD2;
	border-radius: 12px;
	margin: 0px 10px;
	font-family: 'GothamPro';
	font-size: 18px;
	text-align: left;
	padding: 0 10px 0 40px;
	position: relative;
	background: url(../images/icons/search.svg) no-repeat left 10px center;
	cursor: pointer;
}
.search input:focus {
	border: 1px solid #C8CDD2;
	outline: none;
}
.filterList {
	display: none;
	position: absolute;
	background: #fff;
	width: calc(100% + 2px);
	left: -1px;
  border: 1px solid #C8CDD2;
  border-top: none;
	padding-top: 10px;
  top: 30px;
  z-index: 15;
  cursor: pointer;
}
.filterList div {
	padding-left: 10px;
	display: flex;
	justify-content: space-between;
    align-items: center;
}
.filterList div img {
	float: right;
	height: 22.5px;
}
.filterList div:hover {
	background: #F1F5F9;
}
.filters .filter.active .filterList {
	display: block;
}
@media (max-width: 1300px) {
	.textSide {
		width: 400px;
	}
	.headSide {
		width: calc(100% - 400px);
	}
}

@media (max-width: 1200px) {
	.headSide h1 {
	  font-size: 100px;
	}
}
@media (max-width: 992px) {
	.hero {
	  display: block;
	}
	.headSide h1 {
	  font-size: 64px;
	  text-align: center;
	  padding: 65px 20px 15px;
	}
	.headSide {
	  width: 100%;
	}
	.textSide {
	  width: calc(100% - 40px);
	  margin: auto;
	  position: relative;
	  bottom: -50px;
	}
	.textSide {
	  padding: 20px;
	}
	.textSide p {
    font-size: 18px;
    line-height: 28px;
	}
	.textSide .button {
	  margin: 15px 0 0;
	}
	.filters {
	  margin-top: 50px;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 30px;
    padding: 0 20px; 
	}
	.filters h1 {
	  margin-right: 0;
	  width: 100%;
	  text-align: center;
	  line-height: 45px;
	}
	.filters .filter {
	  width: calc(50% - 10px);
	  margin: 0px 5px;
	}
	.search {
		margin: 20px 0;
		width: calc(100% - 10px);
	}
	.search input {
		width: calc(100%);
		margin: 0;
	}

	.filters {
		height: 170px;
	}


}

@media (max-width: 480px) {
	.headSide h1 {
	  font-size: 50px;
	}
	.filters {
	  height: 205px;
	}
	.search {
		margin: 0;
	}


	.filters .filter {
	  width: 100%;
	  margin-bottom: 20px;
	}
}
