.wrapper {
  overflow: hidden;
}
.cards {
  max-width: 1440px;
  margin: auto;
	display: flex;
  align-items: center;
  padding: 0 30px;
  justify-content: space-between;
  flex-wrap: wrap;
}
.cards h1 {
	font-family: 'GothamPro-Bold';
	font-size: 28px;
	line-height: 27px;
	color: #E85656;
	display: block;
	width: 100%;
	margin-bottom: 20px;
	padding: 0 10px;
}
.card {
	width: 300px;
	height: 450px;
	background: #F1F5F9;
	margin-bottom: 30px;
	border-radius: 12px;
	overflow: hidden;
	margin: 0 10px 30px;
}
.image	{
	height: 200px;
	text-align: center;
	line-height: 200px;
}
.image	img	{
	max-width: 100%;
	max-height: 100%;
	vertical-align: top;
}
.text {
	padding: 20px;
}
.text h2 {
	font-family: 'GothamPro-Bold';
	font-size: 20px;
	line-height: 20px;
	color: #000;
	margin-bottom: 15px;
	height: 60px;
}
.text p {
	font-family: 'GothamPro';
	font-size: 14px;
	line-height: 19px;
	color: #000;
	margin-bottom: 15px;
	height: 80px;
	overflow: hidden;
	text-overflow: ellipsis;
}
.btns {
  display: flex;
  justify-content: space-between;
}
.btns .button {
	width: 100%;
	height: 40px;
	border: 1px solid #E85656;
	font-family: 'GothamPro';
	font-size: 14px;
	line-height: 40px;
	border: 1px solid #E85656;
	display: block;
	text-decoration: none;
	text-align: center;
	border-radius: 12px;
}
.btns .button.more {
	color: #E85656;
	background: transparent;
}
.btns .button.more:hover {
	color: #fff;
	background: #E85656;
}

@media (max-width: 992px) {
	.cards {
	  padding: 0 20px;
	  justify-content: center;
	  align-items: center;
	}
}
@media (max-width: 700px) {
	.card,
	.text p,
	.text h2 {
		height: auto;
	}
}
