.donate {
	width: 45%;
}
.donateBox {
	padding: 50px 50px 75px;
	background: url('../../images/icons/white-arrow.svg') no-repeat bottom 20px center #E85656;
	background-size: 26px;
	border-top-left-radius: 12px;
	border-top-right-radius: 12px;
}
.donateBox h3 {
	font-family: 'GothamPro';
	font-size: 28px;
	line-height: 27px;
	color: #fff;
	padding-bottom: 10px;
}
.donateBox p {
	font-family: 'GothamPro';
	font-size: 18px;
	line-height: 29px;
	color: #fff;
}
.donateList {
	padding: 30px 50px;
	background: #F1F1F1;
	border-bottom-left-radius: 12px;
	border-bottom-right-radius: 12px;
}
.donateItem__head {
	font-family: 'GothamPro-Bold';
	font-size: 18px;
	line-height: 30px;
	color: #000;
	display: block;
}
.donateItem__item {
	font-family: 'GothamPro';
	font-size: 18px;
	line-height: 55px;
	height: 55px;
	text-align: left;
	padding-left: 15px;
	padding-right: 50px;
	color: #4E555B;
	display: block;
	background: #E7E7E7;
	border-radius: 12px;
	position: relative;
}
.donateItem__text {
	display: block;
	overflow: hidden;
	text-overflow: ellipsis;
  font-family: 'GothamPro';
  font-size: 18px;
  line-height: 55px;
  height: 55px;
  text-align: left;
  color: #4E555B;
  background: #E7E7E7;
  border: none;
  width: 100%;
}
.donateItem__text:focus,
.donateItem__text:focus-visible {
	border: none;
	outline: none;
}
.hidden {
	display: none;
}
.donateList__box {
	margin-bottom: 25px;
}
.donateItem__icon {
	position: absolute;
	top: 0; right: 0;
	width: 50px;
	height: 55px;
}
.link_icon {
	background: url(../../images/icons/link.svg) no-repeat center #E7E7E7;
	border-top-right-radius: 12px;
	border-bottom-right-radius: 12px;
	cursor: pointer;
}
@media(max-width: 1200px){
	.about-main__text {
		padding-right: 50px;
	}
}
@media(max-width: 992px){
	.donate {
		width: 100%;
	}
}
@media(max-width: 768px){
	.donateBox h3 {
    font-size: 25px;
    line-height: 25px;
	}
	.donateBox {
    padding: 20px 20px 45px;
    background-size: 20px;
	}
	.donateList {
	  padding: 30px 20px;
	}
}
