.contacts {
	display: flex;
	flex-direction: row;
}
.header {
	font-family: 'GothamPro-Bold';
	font-size: 28px;
	line-height: 27px;
	color: #E85656;
}
.box {
	width: 50%;
	display: block;
}
.box a,
.box span {
	display: block;
	padding-left: 30px;
	font-family: 'GothamPro';
	font-size: 16px;
	line-height: 22px;
	color: #000;
	margin-bottom: 20px;
	text-decoration: none;
	text-overflow: ellipsis;
    overflow: hidden;
    width: calc(100% - 20px);
    white-space: nowrap;
}

.box .mail {
	background: url('../../images/icons/mail.svg') no-repeat left center;
}
.box .site {
	background: url('../../images/icons/site.svg') no-repeat left center;
}
.box .facebook {
	background: url('../../images/icons/facebook.svg') no-repeat left 3px center;
}
.box .instagram {
	background: url('../../images/icons/instagram.svg') no-repeat left center;
}
.box .telegram {
	background: url('../../images/icons/telegram.svg') no-repeat left center;
}
.box .twitter {
	background: url('../../images/icons/twitter.svg') no-repeat left center;
}

@media(max-width: 768px){
	.contacts {
	  display: block;
	}
	.box {
	  width: 100%;
	}
}
