.header {
	font-family: 'GothamPro-Bold';
	font-size: 28px;
	line-height: 27px;
	color: #E85656;
	padding: 0 0 20px;
}
.paragraph {
	font-family: 'GothamPro';
	font-size: 16px;
	line-height: 29px;
	color: #000;
	margin-bottom: 20px;
}
@media(max-width: 768px) {
	.header {
    font-size: 18px;
    line-height: 25px;
	}
	.paragraph {
    font-size: 14px;
    line-height: 25px;
	}
}