.header {
	height: 90px;
	background: #fff;
	padding: 0 40px;
	display: flex;
	justify-content: space-between;
  align-items: center;
  max-width: 1440px;
  margin: auto;
}
.logo {
	width: 250px;
	height: 90px;
	background: url(../../images/logo.svg) no-repeat center;
	background-size: contain;
}
.link {
	font-family: 'GothamPro';
	font-size: 16px;
	line-height: 15px;
	color: #2B3137;
	text-decoration: none;
	padding: 0 15px;
}
.link.active {
	font-family: 'GothamPro-Bold';
	color: #E85656;
}
.switch {
	font-family: 'GothamPro';
	font-size: 16px;
	line-height: 30px;
	color: #686565;
	height: 30px;
	padding-left: 30px;
	padding-right: 30px;
	background: url(../../images/icons/globe.svg) no-repeat left center;
	position: relative;
	cursor: pointer;
	min-width: 150px;
}
.switchList {
	background: #fff;
	text-align: right;
	display: none;
	position: absolute;
	left: 0;
	width: 100%;
	text-align: left;
	padding: 10px 30px;
	border-top: none;
	z-index: 11;

}
.switchList.open {
	display: block;
}
.mobile {
	display: none;
}

@media (max-width: 992px) {
	.header {
		padding: 0 20px;
		height: 60px;
	}
	.logo {
		width: 170px;
		height: 60px;
		background: url(../../images/logo.svg) no-repeat center;
		background-size: contain;
	}
	.desktop {
		display: none;
	}
	.mobile {
		display: block;
	}
	.mobileButton {
		width: 30px;
		height: 30px;
		background: url(../../images/icons/menu.svg) no-repeat center;
	}
	.closeIcon {
		width: 30px;
		height: 30px;
		right: 20px;
		top: 15px;
		position: absolute;
	}
	.closeIcon span {
		display: block;
		position: absolute;
		width: 30px;
		height: 2px;
		background: #fff;
		top: 15px;
	}
	.closeIcon span:first-child {
	  transform: rotate(45deg);
	}
	.closeIcon span:last-child {
		transform: rotate(-45deg);
	}
	.nav.mobile {
		position: fixed;
		width: 100vw;
		height: 100vh;
		background: #2E3339;
		left: 0; top: 0;
		z-index: 99;
		padding-top: 100px;
		text-align: center;
		display: none;
	}
	.nav.mobile.open {
		display: block;
	}
	.nav.mobile .link {
		font-size: 32px;
		color: #fff;
		display: block;
    line-height: 2;
    text-align: center;
	}
	.nav.mobile .link.active {
		color: #E85656;
	}
	.nav.mobile .switch {
		color: #BFBFBF;
		display: inline-block;
		margin-top: 50px;
		min-width: 1px;
	}
	.nav.mobile .switchList {
	  background: transparent;
	  text-align: center;
	}
}
