.toTop {
	padding: 25px 7.5px 7.5px 7.5px;
    background: #E85656;
    border-radius: 12px;
    position: fixed;
    right: 20px;
    bottom: 20px;
	font-family: 'GothamPro';
    font-size: 14px;
    line-height: 25px;
    color: #fff;
    text-decoration: none;
    z-index: 20;
    cursor: pointer;
}
.toTop:after {
	background: url('../../images/icons/white-arrow.svg') no-repeat center;
	position: absolute;
    width: 13px;
    height: 13px;
	background-size: 13px;
    content: '';
	top: 7px;
    transform: rotate(180deg);
    left: calc(50% - 6.5px);
}
