.modal {
	padding: 20px;
    width: 300px;
    background: #E85656;
    border-radius: 12px;
    position: fixed;
    left: calc(50% - 150px);
    top: 50px;
	font-family: 'GothamPro';
    font-size: 14px;
    line-height: 25px;
    color: #fff;
    text-decoration: none;
    z-index: 100;
    cursor: pointer;
}
.modal h2 {
    padding-top: 30px;
    padding-bottom: 20px;
}
.modalBg {
    backdrop-filter: blur(5px);
	position: fixed;
    width: 100%;
    height: 100%;
	top: 0;
    left: 0;
    z-index: 99;
}
.link {
    color: #fff;
    text-decoration: underline;
}
.link:hover {
    color: #fff;
    text-decoration: none;
}
.close {
    width: 25px;
    height: 25px;
    right: 15px;
    top: 15px;
    position: absolute;
}
.close:before,
.close:after {
    display: block;
    position: absolute;
    width: 25px;
    height: 2px;
    background: #fff;
    top: 15px;
    content: '';
}
.close:before{
    transform: rotate(45deg);
}
.close:after{
    transform: rotate(-45deg);
}