.content {
	max-width: 1440px;
	margin: auto;
}

.hero {
	padding: 50vh 40px 0;
	position: relative;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

.hero:after {
	background: linear-gradient(359.65deg, rgba(0, 0, 0, 0.6) 0.37%, rgba(0, 0, 0, 0) 99.77%),
				linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
	content: "";
	position: absolute;
	left: 0; top: 0;
	width: 100%;
	height: 100%;
	backdrop-filter: blur(2px);
}

.hero h1 {
	font-family: 'GothamPro-Bold';
	font-size: 76px;
	line-height: 1;
	text-align: center;
	color: #fff;
	position: relative;
	z-index: 10;
}

.tags {
	height: 140px;
	display: flex;
	justify-content: center;
	aligns: center;
	flex-direction: row;
	padding: 50px 0;
	position: relative;
	z-index: 10;	
}

.tags div {
	width: 130px;
	height: 40px;
	background: rgba(0, 0, 0, 0.2);
	border-radius: 12px;
	margin: 0px 15px;
	font-family: 'GothamPro';
	font-size: 14px;
	line-height: 40px;
	color: rgba(255, 255, 255, 0.8);
	text-align: center;
}

.tags .country {
	width: auto;
	height: 40px;
	background: transparent;
}

.tags .country img {
	height: 100%;
}

.main {
	padding: 40px;
	display: flex;
  flex-direction: row;
}

.main__text {
	width: 55%;
	padding-right: 100px;
}

.main__donate {
	width: 45%;
}

.main__text h2 {
	padding: 0 0 20px;
}

.box {
	padding: 50px 50px 75px;
	background: url('../images/icons/white-arrow.svg') no-repeat bottom 20px center #E85656;
	background-size: 26px;
	border-top-left-radius: 12px;
	border-top-right-radius: 12px;
}

.box h3 {
	font-family: 'GothamPro';
	font-size: 28px;
	line-height: 27px;
	color: #fff;
	padding-bottom: 10px;
}

.box p {
	font-family: 'GothamPro';
	font-size: 18px;
	line-height: 29px;
	color: #fff;
}

.donate {
	padding: 30px 50px;
	background: #F1F1F1;
	border-bottom-left-radius: 12px;
	border-bottom-right-radius: 12px;
}

.donate__head {
	font-family: 'GothamPro-Bold';
	font-size: 18px;
	line-height: 30px;
	color: #000;
	display: block;
}

.donate__item {
	font-family: 'GothamPro';
	font-size: 18px;
	line-height: 55px;
	height: 55px;
	text-align: left;
	padding-left: 15px;
	padding-right: 50px;
	color: #4E555B;
	display: block;
	background: #E7E7E7;
	border-radius: 12px;
	position: relative;
}

.donate__text {
	display: block;
	overflow: hidden;
	text-overflow: ellipsis;
  font-family: 'GothamPro';
  font-size: 18px;
  line-height: 55px;
  height: 55px;
  text-align: left;
  color: #4E555B;
  background: #E7E7E7;
  border: none;
  width: 100%;
}
.donate__text:focus,
.donate__text:focus-visible {
	border: none;
	outline: none;
}
.hidden {
	display: none;
}

.donate__box {
	margin-bottom: 25px;
}

.donate__icon {
	position: absolute;
	top: 0; right: 0;
	width: 50px;
	height: 55px;
}
.copy_icon {
	background: url(../images/icons/copy.svg) no-repeat center #E7E7E7;
	border-top-right-radius: 12px;
	border-bottom-right-radius: 12px;
	cursor: copy;
}

.link_icon {
	background: url(../images/icons/link.svg) no-repeat center #E7E7E7;
	border-top-right-radius: 12px;
	border-bottom-right-radius: 12px;
	cursor: pointer;
}

@media(max-width: 1200px){
	.main__text {
		padding-right: 50px;
	}
}
@media(max-width: 992px){
	.main {
	  display: block;
	}
	.main__text {
		padding-right: 0;
		width: 100%;
		margin-bottom: 40px;
	}
	.main__donate {
		width: 100%;
	}
}
@media(max-width: 768px){
	.hero h1 {
	  font-size: 30px;
	}
	.main__head {
    font-size: 18px;
    line-height: 25px;
	}
	.main__paragraph {
    font-size: 14px;
    line-height: 25px;
	}

	.main__text h2 {
    font-size: 18px;
    line-height: 25px;
	}
	.box h3 {
    font-size: 25px;
    line-height: 25px;
	}
	.box {
    padding: 20px 20px 45px;
    background-size: 20px;
	}
	.main__contacts {
	  display: block;
	}
	.main__contacts-box {
	  width: 100%;
	}
	.donate {
	  padding: 30px 20px;
	}
	.main {
	  padding: 20px;
	}
	.tags div {
	  margin: 0px 7px;
	}
}
@media(max-width: 480px){
	.tags {
    flex-direction: column;
    height: auto;
    padding: 30px 0;
    align-items: center;
	}
	.tags div {
	  margin: 7px 0px;
	}
}


