.toSearch {
	padding: 7.5px 10px 7.5px 25px;
    background: #E85656;
    border-radius: 12px;
    position: fixed;
    right: 20px;
    bottom: 20px;
	font-family: 'GothamPro';
    font-size: 14px;
    line-height: 25px;
    color: #fff;
    text-decoration: none;
    z-index: 20;
}
.toSearch:after {
	background: url('../../images/icons/white-arrow.svg') no-repeat center;
	position: absolute;
	width: 13px;
	height: 13px;
	background-size: 13px;
    content: '';
	left: 7px;
    transform: rotate(90deg);
    top: calc(50% - 6.5px);
}