.content {
	max-width: 1440px;
	margin: auto;
}
.hero {
	padding: 50vh 40px 0;
	position: relative;
	background-image: url('../images/logos/empty.png');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}
.hero:after {
	background: linear-gradient(359.65deg, rgba(0, 0, 0, 0.6) 0.37%, rgba(0, 0, 0, 0) 99.77%),
				linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
	content: "";
	position: absolute;
	left: 0; top: 0;
	width: 100%;
	height: 100%;
}
.hero h1 {
	font-family: 'GothamPro-Bold';
	font-size: 76px;
	line-height: 1;
	text-align: center;
	color: #fff;
	position: relative;
	z-index: 10;
}
.tags {
	height: 140px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
	padding: 50px 0;
	position: relative;
	z-index: 10;	
}
.tags div {
	width: 130px;
	height: 40px;
	background: rgba(0, 0, 0, 0.2);
	border-radius: 12px;
	margin: 0px 15px;
	font-family: 'GothamPro';
	font-size: 14px;
	line-height: 40px;
	color: rgba(255, 255, 255, 0.8);
	text-align: center;
}
.tags .country {
	width: auto;
	height: 40px;
	background: transparent;
}
.tags .country img {
	height: 100%;
}
.main {
	padding: 40px;
	display: flex;
  flex-direction: row;
}

.text {
	width: 55%;
	padding-right: 100px;
}

.text h2 {
	padding: 0 0 20px;
}

.link_icon {
	background: url(../images/icons/link.svg) no-repeat center #E7E7E7;
	border-top-right-radius: 12px;
	border-bottom-right-radius: 12px;
	cursor: pointer;
}

@media(max-width: 1200px){
	.text {
		padding-right: 50px;
	}
}
@media(max-width: 992px){
	.main {
	  display: block;
	}
	.text {
		padding-right: 0;
		width: 100%;
		margin-bottom: 40px;
	}
}
@media(max-width: 768px){
	.hero h1 {
	  font-size: 30px;
	}
	.main {
	  padding: 20px;
	}
	.tags div {
	  margin: 0px 7px;
	}
}
@media(max-width: 480px){
	.tags {
    flex-direction: column;
    height: auto;
    padding: 30px 0;
	}
	.tags div {
	  margin: 7px 0px;
	}
}

